import React, { useState, useCallback, useEffect, useRef } from "react";
import AddForm from "./AddForm";
import Todo from "./Todo";
import styled from "styled-components";

const StyledTodoList = styled.div`
  .todolist-box {
    width: 400px;
    height: 500px;
    /* background-color: rgba(255,255,255,0.7); */
    display: flex;
    flex-direction: column;
    align-items: center;
    /* border-radius: 20px; */
    /* box-shadow: 0px 0px 5px rgba(0,0,0, 0.1); */
  }
  .header {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
  }
  .info {
    width: 85%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    margin-top: 20px;
  }
  .info > span {
    font-weight: bold;
    /* display: none; */
  }
  .info > span::selection {
    color: green;
  }
  h1::selection {
    color: orange;
  }
  h1 {
    margin: 0;
    font-size: 22px;
    line-height: 50px;
  }

  .date {
    width: 85%;
    font-size: 13px;
    font-weight: 600;
  }
  .date > span::selection {
    color: orange;
  }
  .day {
    margin-left: 5px;
  }
  .day::selection {
    color: orange;
  }
  ul {
    padding: 0;
    height: 340px;
    overflow-x: hidden;
    overflow-y: scroll;
  }
  ul::-webkit-scrollbar {
    display: none;
  }
`;

const TodoList = () => {
  const [todoList, setTodoList] = useState(() => {
    const localTodoList = localStorage.getItem("todoList");
    return localTodoList ? JSON.parse(localTodoList) : [];
  });
  const [id, setId] = useState(0);
  const isMount = useRef(true);

  useEffect(() => {
    if (!isMount.current) {
      localStorage.setItem("todoList", JSON.stringify(todoList));
      localStorage.setItem("id", id);
    }
  }, [todoList, id]);

  useEffect(() => {
    const localTodoList = localStorage.getItem("todoList");
    if (localTodoList) {
      setTodoList(JSON.parse(localTodoList));
    }
    const localId = localStorage.getItem("id");
    if (localId) {
      setId(parseInt(localId));
    }
    isMount.current = false;
  }, []);

  const addTodo = useCallback(
    (todo) => (e) => {
      console.log("add");
      e.preventDefault();
      if (todo) {
        const currentDate = new Date().toLocaleDateString();
        setTodoList((prevTodoList) => [
          ...prevTodoList,
          {
            id: id,
            todo: todo,
            isChecked: false,
            date: currentDate,
          },
        ]);
        setId((prevId) => prevId + 1);
        localStorage.setItem("todoList", JSON.stringify(todoList));
        localStorage.setItem("id", id);
      }
    },
    [id, todoList]
  );

  const updateTodo = useCallback(
    (id, todo, isChecked) => {
      const index = todoList.findIndex((todoInfo) => todoInfo.id === id);
      const newTodoList = [...todoList];
      newTodoList.splice(index, 1, {
        id: id,
        todo: todo,
        isChecked: isChecked,
      });
      setTodoList(newTodoList);
    },
    [todoList]
  );

  const deleteTodo = useCallback(
    (id) => () => {
      const newTodoList = todoList.filter((todoInfo) => todoInfo.id !== id);
      setTodoList(newTodoList);
      localStorage.setItem("todoList", JSON.stringify(newTodoList));
      localStorage.setItem("id", id);
    },
    [todoList]
  );

  const toggleCheck = useCallback(
    (id) => () => {
      const index = todoList.findIndex((todoInfo) => todoInfo.id === id);
      const newTodoList = [...todoList];
      newTodoList[index].isChecked = newTodoList[index].isChecked
        ? false
        : true;
      setTodoList(newTodoList);
    },
    [todoList]
  );

  const today = new Date();
  const dateString = today.toLocaleString("ko-KR", {
    year: "numeric",
    month: "long",
    day: "numeric",
  });
  const dayName = today.toLocaleString("ko-KR", { weekday: "long" });

  //ÎÇ®ÏùÄ Ìï†Ïùº Í∞ØÏàò Î≥¥Í∏∞
  // const todos = useTodoState();
  // const undoneTasks = todos.filter(todo => !todo.done);
  //{undoneTasks.length}Í∞ú
  const undone = todoList.filter((todo) => !todo.isChecked);

  return (
    <StyledTodoList>
      <div className="todolist-box">
        <div className="header">
          <div className="info">
            <h1>Ïò§ÎäòÏùò Ìï† Ïùº Î™©Î°ù</h1>
            <span className="undone">{undone.length}Í∞ú </span>
          </div>
          <div className="date">
            <span>{dateString}</span>
            <span className="day">{dayName}</span>
          </div>
        </div>
        <AddForm addTodo={addTodo} />
        <ul>
          {todoList.map((todoInfo) => {
            return (
              <Todo
                key={todoInfo.id}
                id={todoInfo.id}
                todo={todoInfo.todo}
                isChecked={todoInfo.isChecked}
                updateTodo={updateTodo}
                deleteTodo={deleteTodo}
                toggleCheck={toggleCheck}
              />
            );
          })}
        </ul>
      </div>
    </StyledTodoList>
  );
};

export default TodoList;
